import React from "react";

function Container({ children }) {
  return (
    <div
      style={{
        padding: 0,
        margin: 0,
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
        height: "100%",
        width: "100%",
      }}
    >
      {children}
    </div>
  );
}

export default Container;
