import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import Avatar from "@mui/material/Avatar";
import DialogTitle from "@mui/material/DialogTitle";
import Dialog from "@mui/material/Dialog";
import Typography from "@mui/material/Typography";
import { Box, DialogContent, Stack } from "@mui/material";
import Slide from "@mui/material/Slide";
import { LoadingButton } from "@mui/lab";
import { useNavigate } from "react-router-dom";

import mtn from "../../assets/images/mtn4.png";
import vodafone from "../../assets/images/vodafone.png";
import airteltigo from "../../assets/images/airtel.png";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

function WaecCheckerPayment({ open, setOpen, payload }) {
  const navigate = useNavigate();

  const [serviceProviderImg, setServiceProviderImg] = useState(null);

  useEffect(() => {
    switch (payload.img) {
      case "mtn":
        setServiceProviderImg(mtn);
        break;
      case "vodafone":
        setServiceProviderImg(vodafone);
        break;
      case "airteltigo":
        setServiceProviderImg(airteltigo);
        break;
      default:
        setServiceProviderImg(null);
    }
  }, [payload.img]);

  const handleClose = () => {
    setOpen(false);
  };

  const PlaceholderItem = ({ title, value }) => {
    return (
      <>
        <Box
          sx={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
          }}
        >
          <Typography
            fontWeight="bold"
            sx={{ fontSize: 14, color: "primary.main" }}
          >
            {title}
          </Typography>
          <Typography variant="body2">{value}</Typography>
        </Box>
      </>
    );
  };

  return (
    <Dialog
      keepMounted
      TransitionComponent={Transition}
      onClose={handleClose}
      open={open}
    >
      <DialogTitle sx={{ textAlign: "center" }}>Payment Details</DialogTitle>
      <DialogContent>
        <Typography variant="body2">
          {payload.examsType} RESULTS CHECKER
        </Typography>
        <Box sx={{ width: 280, padding: 2 }}>
          <Stack spacing={2} paddingBottom={6}>
            <PlaceholderItem title="Quantity" value={payload.quantity} />
            <PlaceholderItem title="Total" value={payload.totalAmount} />
            <Avatar
              src={serviceProviderImg}
              variant="square"
              sx={{ width: 80, height: 40, marginY: 4, alignSelf: "center" }}
            />

            <PlaceholderItem title="Email" value={payload.email} />
            <PlaceholderItem title="Phone Number" value={payload.phoneNumber} />
          </Stack>
          <LoadingButton
            disableElevation
            fullWidth
            variant="contained"
            size="large"
            onClick={() => navigate("/checkout", { replace: true })}
          >
            Proceed to buy
          </LoadingButton>
        </Box>
      </DialogContent>
    </Dialog>
  );
}

WaecCheckerPayment.propTypes = {
  open: PropTypes.bool.isRequired,
  setOpen: PropTypes.func,
};

export default WaecCheckerPayment;
