import { Box, Grid, Typography } from "@mui/material";

import ges from "../assets/images/ges.jpg";
import ecg from "../assets/images/ecg.jpg";
import airtime from "../assets/images/airtime3.jpg";

import "../styles/App.scss";
import ShopCard from "./ShopCard";

const homeCardArr = [
  {
    id: 1,
    title: "E-VOUCHERS & PINCODES",
    img: ges,
    content: `Buy
    BECE results checker
    SSCE, WASSCE, WASSCE NOVDEC, ABCE checker`,
    path: "evoucher",
  },
  {
    id: 2,
    title: "PREPAID UNITS",
    img: ecg,
    content: `Buy
    BECE results checker
    SSCE, WASSCE, WASSCE NOVDEC, ABCE checker`,
    path: "prepaid",
  },
  {
    id: 3,
    title: "BULK AIRTIMES & EVD's",
    img: airtime,
    content: `Buy
    BECE results checker
    SSCE, WASSCE, WASSCE NOVDEC, ABCE checker`,
    path: "airtime",
  },
];

function HomeCards() {
  return (
    <Box
      sx={{
        paddingY: 10,
        paddingX: 5,
        backgroundColor: "#fff",
      }}
    >
      <Typography
        variant="h4"
        sx={{
          textAlign: "center",
          paddingBottom: 5,
        
        }}
      >
        Our Services
      </Typography>

      <Grid container spacing={3}>
        {homeCardArr.map((home) => {
          return (
            <Grid item sm={6} md={4} key={home.id}>
              <ShopCard {...home} />
            </Grid>
          );
        })}
      </Grid>
    </Box>
  );
}

export default HomeCards;
