import { CardGiftcardRounded, CardTravel, Home } from "@mui/icons-material";
import { BottomNavigation, BottomNavigationAction, Paper } from "@mui/material";
import React from "react";

function BottomNav() {
  const [value, setValue] = React.useState(0);
  return (
    <Paper
      sx={{
        display: { xs: "block", md: "none" },
        position: "fixed",
        bottom: 0,
        left: 0,
        right: 0,
        zIndex:100
      }}
      elevation={3}
    >
      <BottomNavigation
        showLabels
        value={value}
        onChange={(event, newValue) => {
          setValue(newValue);
        }}
      >
        <BottomNavigationAction label="Recents" icon={<Home />} />
        <BottomNavigationAction
          label="Favorites"
          icon={<CardGiftcardRounded />}
        />
        <BottomNavigationAction label="Archive" icon={<CardTravel />} />
        <BottomNavigationAction label="Archive" icon={<CardTravel />} />
      </BottomNavigation>
    </Paper>
  );
}

export default BottomNav;
